<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process144"></a>

<p><br></p>
<p><span class="rvts17">主线：“女士”大战（</span><span class="rvts50">The Lady o' War</span><span class="rvts17">）</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:584,Y:285</span><span class="rvts21">，地点：复仇女士号</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">保护马拉迪，为她争取吟唱时间</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1089.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">当复仇女士号启动以后，便遭到了达利斯的围捕。一场达利斯和马拉迪之间的女士大战一触即发。</span>
<br><span class="rvts21">该战役看似艰难，其实只是一个过场而已。需要做的就是尽可能的保护马拉迪，为她争取吟唱回音大厅的时间。</span>
<br><span class="rvts21">（如果意外的把达利斯打到了</span><span class="rvts20">400</span><span class="rvts21">血量以下，那么达利斯会立马告诉手下撤退。）</span>
<br><span class="rvts21">在打斗进入到第</span><span class="rvts20">3</span><span class="rvts21">或第</span><span class="rvts20">4</span><span class="rvts21">回合后，马拉迪会释放一个源力爆发，就像时间虫洞一样，使进入了另外一个世界。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



